import React, { useState } from 'react';
import Topwave from './topwave/Topwave';
import Titre from '../Title/Titre';
import Botwave from './botwave/Botwave';
import Lottie from 'lottie-react';
import anime from './animation.json';
import Poles from './pole/Poles';
import Grid from './grid/Grid';
import img1 from '../../images/study.jpg';

// commercial
const dlogistique = "Cette orientation consiste à former de futurs managers qui seront à même de gérer toutes les opérations de la chaîne logistique et du transport maritime";
const dlogistique2 = "La finalité de cette orientation est de proposer aux étudiants issus de cursus à dominante assurance ou droit une double compétence( banque et assurance)";
const dlogistique3 = "Cette orientation consiste à former de futurs managers qui seront à même de gérer toutes les opérations de la chaîne logistique et du transport maritime";
const data = [
   { image: img1, title: 'Transport et logistique', info: dlogistique },
   { image: img1, title: 'Comptabilité & Gestion', info: dlogistique2 },
   { image: img1, title: 'Management Ressource H.', info: dlogistique2 },
   { image: img1, title: 'Qualité  logistique', info: dlogistique2 },
   { image: img1, title: 'Négociation & digitalisation', info: dlogistique2 },
];

// technique

const text1 ="Cette orientation consiste à former des futurs sous ingenieurs en: forage, Réservoirs, production, géosciences,  qui seront à même d’assister les ingénieurs pour ensuite diriger d’une manière autonome des sociétés opératrices, compagnies de service dans l’espace économique Africain francophone tout en respectant les normes internationales"
const data2 =[
   { image: img1, title: 'Maintenance industrielle', info: text1 },
   { image: img1, title: 'Génie pétrolier et minier', info: text1 },
   { image: img1, title: 'Génie pétrolier et minier', info: text1 },
   { image: img1, title: 'Génie pétrolier et minier', info: text1 },
]



export default function Formations() {

   const [selectedItem, setSelectedItem] = useState(null);
   const handleMoreInfoClick = (item) => {
      setSelectedItem(item);
   };

   const handleClosePopup = () => {
      setSelectedItem(null);
   };

   return (
      <>
         <section className="sectionTwo">
            <Topwave />
            <div className="sonThree">
               <div id="forming" className="protector">
                  <div style={{ width: '100%', display: 'flex', flexDirection: 'column' }}>
                     <Lottie
                        style={{ width: '250px', height: '250px', margin: '0 auto' }}
                        className="lottie-animd animate-on-scroll"
                        animationData={anime}
                     />
                  </div>
                  <Titre titre="les diplomes de HEMIP" />
                  <div className="poles">
                     <div className="poletech">
                        <Poles part="Pole technique" />
                        <div className="grids">
                           <Grid data={data2} onMoreInfoClick={handleClosePopup}/>
                        </div>
                     </div>
                     
                     <div className="polecom">
                        <Poles part="Pole commercial" />
                        <div className="grids">
                           <Grid data={data} onMoreInfoClick={handleMoreInfoClick} />
                        </div>
                     </div>
                  </div>
               </div>
            </div>
            <Botwave />
         </section>

         {selectedItem && (
            <div className="popup">
               <div className="popup-content">
                  <h3>{selectedItem.title}</h3>
                  <p>{selectedItem.info}</p>
                  <button onClick={handleClosePopup}>Fermer</button>
               </div>
            </div>
         )}
      </>
   );
}
